body { 
  font-family: 'Chelsea Market', cursive;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #f4f4f4;
  color: #333;
  font-size: 16px;
}

.yellow-box {
  background-color: #FBC02D;
  padding: 20px;
  border-radius: 20px;
  margin: 20px auto;
  max-width: 1200px;
  color: #1C1C1C;
}

.green-box {
  background-color: #388E3C;
  color: white;
  padding: 20px;
  border-radius: 15px;
  margin-top: 20px;
}

.green-box hr {
  border-color: #fff;
  margin-top: 10px;
  margin-bottom: 10px;
}

.mx-auto {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

input[type="text"], .slider {
  padding: 10px;
  font-size: 1.2rem;
  margin-top: 10px;
  width: 100%;
  border-radius: 8px;
  border: 1px solid #ddd;
}

input[type="text"]:focus {
  outline: none;
  border-color: #388E3C;
}

h1 {
  text-align: center;
  color: #1C1C1C;
  font-size: 2.5rem;
  font-weight: bold;
  margin-top: 0;
  margin-bottom: 20px;
}

.green-box .text-white {
  color: white;
  font-size: 1.5rem;
  font-weight: bold;
}

label {
  font-weight: 600;
  font-size: 1.1rem;
  color: #1427B8;
  margin-bottom: 8px;
  display: block;
}

.space-y-8 {
  margin-top: 20px;
}

p {
  font-size: 1rem;
  color: #555;
  margin-top: 10px;
}

.slider {
  background-color: #eee;
  margin-top: 10px;
}

.slider {
  border-radius: 10px;
  height: 8px;
  background: #388E3C;
}

.slider::-webkit-slider-thumb {
  border-radius: 50%;
  background: #1C1C1C;
  border: none;
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  border-radius: 50%;
  background: #1C1C1C;
  border: none;
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.text-sm {
  font-size: 0.9rem;
}

.text-center {
  text-align: center;
}

.mt-2 {
  margin-top: 20px;
}

.py-4 {
  padding-top: 16px;
  padding-bottom: 16px;
}

.px-6 {
  padding-left: 24px;
  padding-right: 24px;
}

.mb-8 {
  margin-bottom: 32px;
}
